@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
  /* height: 100%;
  width: 100%;
  margin: 0;
  padding: 0; */
  overflow-x: hidden;
}

#mainDiv {
  background-color: rgb(67, 32, 80);
  background-image: url(./assets/site-bg.jpg);
  background-blend-mode: h;
}

.btn {
  /* background: linear-gradient(
    92.23deg,
    #b0c592 12%,
    #bbbe64 117.04%
  ); */
  color: #f3e8f2;
  font-weight: bold;
  border: 3px solid #fff;
  transition: all .4s ease;
  border-color: rgba(255,255,255,.1);
  
}

.btn:hover {
  border-color: #f3e8f285;
  transform: scale(0.96);
  box-shadow: 1px 8.42184px 120.23px rgba(166, 128, 164, 0.51);
}

.gradient,
.active {
  background: linear-gradient(
    92.23deg,
    #767da7 11%,
    #ad47a8 86.04%
  );
}

.active {
  color: #fff;
  padding: 1rem;
  border-radius: 100%;
}

@layer base {
  body {
    @apply font-primary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-lg font-tertiary text-white font-medium;
  }
  .btn-sm {
    @apply h-[50px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#ad47a8] to-[#767da7] font-bold font-primary hover:from-[#767da7] hover:to-[#ad47a8];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
